"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet";
import { Menu, X } from "lucide-react";
import { exerciciosPorTreino } from "./components/ExercicioList";

export default function WorkoutApp() {
  const [selectedTreino, setSelectedTreino] = useState<string | null>(null);
  const [selectedVideo, setSelectedVideo] = useState<{ nome: string; url: string } | null>(null);
  const [isOpen, setIsOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");

  const treinosNomes = Object.keys(exerciciosPorTreino);
  const filteredExercicios = selectedTreino
    ? exerciciosPorTreino[selectedTreino].filter((exercicio) =>
        exercicio.nome.toLowerCase().includes(searchQuery.toLowerCase())
      )
    : [];

  const [exercicioTimers, setExercicioTimers] = useState<{ [key: string]: number }>({});
  const [activeTimers, setActiveTimers] = useState<{ [key: string]: any }>({});
  const [totalTime, setTotalTime] = useState(0);

  const handleTimerToggle = (nome: string) => {
    if (activeTimers[nome]) {
      clearInterval(activeTimers[nome]);
      setActiveTimers((prev) => ({ ...prev, [nome]: null }));
    } else {
      const interval = setInterval(() => {
        setExercicioTimers((prev) => ({ ...prev, [nome]: (prev[nome] || 0) + 1 }));
      }, 1000);
      setActiveTimers((prev) => ({ ...prev, [nome]: interval }));
    }
  };

  useEffect(() => {
    const total = Object.values(exercicioTimers).reduce((acc, curr) => acc + curr, 0);
    setTotalTime(total);
  }, [exercicioTimers]);

  const Sidebar = () => (
    <ScrollArea className="h-full">
      <div className="p-4">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-bold">Treinos</h2>
          <Button variant="ghost" size="icon" onClick={() => setIsOpen(false)} className="lg:hidden">
            <X className="h-4 w-4" />
          </Button>
        </div>
        <div className="space-y-2">
          {treinosNomes.map((treino) => (
            <Button
              key={treino}
              variant={selectedTreino === treino ? "default" : "ghost"}
              className="w-full justify-start"
              onClick={() => {
                setSelectedTreino(treino);
                setIsOpen(false);
              }}
            >
              {treino.replace("_", " ")}
            </Button>
          ))}
        </div>
      </div>
    </ScrollArea>
  );

  return (
    <div className="flex h-screen bg-gray-100">
      {/* Desktop Sidebar */}
      <div className="hidden lg:block w-64 bg-white shadow-lg h-full">
        <Sidebar />
      </div>

      {/* Mobile Sidebar */}
      <Sheet open={isOpen} onOpenChange={setIsOpen}>
        <SheetTrigger asChild>
          <Button variant="ghost" size="icon" className="lg:hidden absolute left-4 top-4">
            <Menu className="h-4 w-4" />
          </Button>
        </SheetTrigger>
        <SheetContent side="left" className="p-0 w-64">
          <Sidebar />
        </SheetContent>
      </Sheet>

      {/* Main Content */}
      <div className="flex-1 p-8 pt-16 lg:pt-8">
        <div className="max-w-5xl mx-auto">
          <div className="flex items-center gap-4 mb-6">
            <Button variant="ghost" size="icon" onClick={() => setIsOpen(!isOpen)} className="hidden lg:flex">
              <Menu className="h-4 w-4" />
            </Button>
            <h1 className="text-2xl font-bold">{selectedTreino ? selectedTreino.replace("_", " ") : "Selecione um treino"}</h1>
          </div>

          {selectedTreino && (
            <input
              type="text"
              placeholder="Pesquisar exercício..."
              className="w-full p-2 mb-4 border rounded"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          )}

          {selectedTreino ? (
            <div className="grid gap-4">
              {filteredExercicios.length === 0 ? (
                <div className="text-gray-500">Nenhum exercício encontrado.</div>
              ) : (
                filteredExercicios.map((exercicio) => (
                  <div key={exercicio.nome} className="flex items-center justify-between p-4 bg-white rounded-lg shadow-md">
                    <div className="flex-1">
                      <span className="font-medium text-lg">{exercicio.nome}</span>
                    </div>
                    <Button onClick={() => handleTimerToggle(exercicio.nome)} className="w-24">
                      {activeTimers[exercicio.nome] ? "Pausar" : "Iniciar"}
                    </Button>
                    <span className="text-sm">{exercicioTimers[exercicio.nome] || 0} s</span>
                  </div>
                ))
              )}
            </div>
          ) : (
            <div className="text-center text-gray-500">Selecione um treino para começar</div>
          )}

          {selectedTreino && filteredExercicios.length > 0 && (
            <div className="mt-6 text-lg font-bold">Tempo total do treino: {totalTime} segundos</div>
          )}
        </div>
      </div>
    </div>
  );
}
